import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp,hiddenRecod } from "../../../../../components/helpers/function";
class ExportPenjualanSalesExelRekap extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    console.log(this.props.data);
    return (
      <>
        <ReactHTMLTableToExcel
           id="test-table-xls-button"
           className="btn btn-success btn-block"
           table="table-to-xls"
           filename="Laporan Transaksi Penjualan Sales Rekap"
           sheet="Laporan Transaksi Penjualan Sales Rekap"
           buttonText="Export Exel"
         />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="4" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN TRANSAKSI PENJUALAN SALES REKAP{" "}
              </th>
            </tr>
                <tr>
                      <th colSpan="4"> Periode  </th>
                </tr>
                <tr>
                      <th colSpan="4"> {localStoragedecryp('tgl_laporan').length === 0 ? null : JSON.parse(localStoragedecryp('tgl_laporan')).tgl_awal + ' s/d ' + JSON.parse(localStoragedecryp('tgl_laporan')).tgl_akhir} </th>
                </tr>
                <tr>
              <th colSpan="4"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="4"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
                <tr>
                  <th colSpan="4"></th>
            </tr>
          </thead>
        {this.props.data.map((item, index) => (
                <>
            <tbody>
                {/* <tr>
                    <td style={{textAlign: "left"}}>Kode Sales : {item._id.kode_sales}</td>
                    <td colSpan="4"> </td>
                </tr> */}
                <tr>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>KODE SALES</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>BERAT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>ONGKOS</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>RUPIAH</td>
                </tr>

              <tr>
                <td>{item.detail[0].kode_sales}</td>
                <td style={{ textAlign: "right" }}>{formatGram(item.detail[0].berat,3)}</td>
                <td style={{ textAlign: "right" }}>{hiddenRecod(item.detail[0].ongkos).toLocaleString("kr-KO")}</td>
                <td style={{ textAlign: "right" }}>{item.detail[0].harga_total.toLocaleString("kr-KO")}</td>
              </tr>
          </tbody>
          <tfoot>
          <tr>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}>
              Total Penjualan :  {item.detail.length} 
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {formatGram(item.detail.map(list => list.berat).reduce((a,b) => a+b,0),2)}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.ongkos).reduce((a,b) => hiddenRecod(a+b),0).toLocaleString("kr-KO")}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.harga_total).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </td>
  
            </tr>
            <tr>
                <td colSpan="4"></td>
            </tr>
            </tfoot>
          </>
          
        ))}
              
        </table>
      </>
    );
  }
}
export default ExportPenjualanSalesExelRekap;
