import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram, getToday, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportLaporanPenjualanSalesRekap = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = "",
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_harga = 0;
  let total_berat = 0;
  let total_onkos = 0;
  let total_baraang = 0;


  doc.setFontSize(15);
  doc.text("LAPORAN PENJUALAN SALES REKAP", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN PENJUALAN SALES REKAP"
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  
  data.forEach((item, index) => {
    tableColumn = [
      // [
        // {
        //   content: `KODE SALES : ${item._id.kode_sales}`,
        //   colSpan: 4,
        // },
      // ],
      [
        {
          content: `KODE SALES`,
          // colSpan: 4,
        },
        {
          content: `BERAT`,
          styles: {
            halign: 'right'
          },
          
        },
        {
          content: `ONGKOS`,
          styles: {
            halign: 'right'
          },
          
        },
        {
          content: `RUPIAH`,
          styles: {
            halign: 'right'
          },
          
        },
      ],
    ];
    // let sub_total = 0;
    // let sub_ongkos = 0;
    // let sub_berat = 0;
    // let sub_harga_total  =0;
    
      let rows = [
        item.detail[0].kode_sales,
        {
          content: formatGram(item.detail.map((detail) => detail.berat || 0).reduce((a,b) => a+b, 0), 2),
          styles: {
            halign: 'right'
          },
        },
        {
          content: parseInt(item.detail.map((detail) => parseInt(detail.ongkos)).reduce((a,b) => a+b, 0)).toLocaleString("kr-KO"),
          styles: {
            halign: 'right'
          },
        },
        {
          content: parseInt(item.detail.map((detail) => parseInt(detail.harga_total)).reduce((a,b) => a+b, 0)).toLocaleString("kr-KO"),
          styles: {
            halign: 'right'
          },
        },
      ];

      total_berat = total_berat + parseFloat(item.detail[0].berat);
      total_onkos = total_onkos + parseInt(item.detail[0].ongkos);
      total_harga = total_harga + parseInt(item.detail[0].harga_total);
      
      // sub_berat = sub_berat + parseFloat(item.detail[0].berat);
      // sub_ongkos = sub_ongkos + parseFloat(item.detail[0].ongkos);
      // sub_harga_total = sub_harga_total + parseFloat(item.detail[0].harga_total);
      tableRows.push(rows)

    total_baraang = total_baraang+item.detail.length;
    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
    let total = [
      {
        content: `Total Penjualan : ${item.detail.length}`,
        colSpan: 1,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${formatGram(item.detail.map((detail) => detail.berat || 0).reduce((a,b) => a+b, 0), 3)}`,
        styles: {
          halign: 'right',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(item.detail.map((detail) => parseInt(detail.ongkos || 0)).reduce((a,b) => a+b, 0)).toLocaleString("kr-KO")}`,
        styles: {
          halign: 'right',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(item.detail.map((detail) => parseInt(detail.harga_total || 0)).reduce((a,b) => a+b, 0)).toLocaleString("kr-KO")}`,
        styles: {
          halign: 'right',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
    ];
    tableRows.push(total);
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      // foot: footRows,
      startY: finalY,
      theme: "plain",
      rowPageBreak: "avoid",
      // pageBreak: "avoid",
      margin: { top: 10 },
      bodyStyles: {
        // lineWidth: 0.02, lineColor: "#000",
        fontSize:8,
      },
      headStyles: {
        fontSize:8,
        // lineWidth: 0.02,
        // lineColor: "#000",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
      tableLineColor: [255, 255, 255],
      tableLineWidth: 1,
    });
    tableRows = [];
    tableColumn = [];
    finalY = doc.autoTableEndPosY() + 20
  });
 
  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 12,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
    
    
    finalY = doc.autoTableEndPosY() + 3;
    tableRows = [];
    // footRows = [];
    // sub_total = 0;
    // sub_harga_gram = 0;
    // sub_harga_atribut = 0;
    // sub_berat_atribut = 0;
    // sub_harga_jual = 0;
    // sub_ongkos = 0;
    // sub_berat = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(`User                        : ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                      : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN PENJUALAN SALES REKAP.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write("<html><head><title>LAPORAN PENJUALAN SALES REKAP</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" + 
  string + "></embed></body></html>");
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportLaporanPenjualanSalesRekap;

