import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class ExportPenjualanSalesExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <>
        <ReactHTMLTableToExcel
           id="test-table-xls-button"
           className="btn btn-success btn-block"
           table="table-to-xls"
           filename="Laporan Transaksi Penjualan Sales Detail"
           sheet="Laporan Transaksi Penjualan Sales Detail"
           buttonText="Export Exel"
         />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="12" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN TRANSAKSI PENJUALAN SALES DETAIL{" "}
              </th>
            </tr>
                <tr>
                      <th colSpan="12"> Periode  </th>
                </tr>
                <tr>
                      <th colSpan="12"> {localStoragedecryp('tgl_laporan').length === 0 ? null : JSON.parse(localStoragedecryp('tgl_laporan')).tgl_awal + ' s/d ' + JSON.parse(localStoragedecryp('tgl_laporan')).tgl_akhir} </th>
                </tr>
                <tr>
              <th colSpan="12"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="12"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
                <tr>
                  <th colSpan="12"></th>
            </tr>
          </thead>
        {this.props.data.map((item, index) => (
                <>
            <tbody>
                <tr>
                    <td style={{textAlign: "left"}}>Kode Sales : {item._id.kode_sales}</td>
                    <td colSpan="11"> </td>
                </tr>
                <tr>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NO FAKTUR</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>KODE DEPT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>KODE BARCODE</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NAMA BARANG</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NAMA ATRIBUT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>BERAT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>ONGKOS</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>HARGA JUAL</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>BERAT ATRIBUT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>HARGA ATRIBUT</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>HARGA GRAM</td>
                    <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>HARGA TOTAL</td>
                </tr>
            {item.detail.map((row, index) => (
              <tr>
                <td>{row.no_faktur_jual}</td>
                <td>{row.kode_dept}</td>
                <td>{row.kode_barcode}</td>
                <td style={{ whiteSpace: "nowrap" }}>
                  {row.nama_barang}
                </td>
                <td>{row.nama_atribut}</td>
                <td style={{ textAlign: "right" }}>{row.berat !== undefined ? formatGram(row.berat,3) : 0}</td>
                <td style={{ textAlign: "right" }}>{row.ongkos !== undefined ? row.ongkos.toLocaleString("kr-KO"): 0}</td>
                <td style={{ textAlign: "right" }}>{row.harga_jual !== undefined ? row.harga_jual.toLocaleString("kr-KO"): 0}</td>
                <td style={{ textAlign: "right" }}>{row.berat_atribut !== undefined ? formatGram(row.berat_atribut,3): 0}</td>
                <td style={{ textAlign: "right" }}>{row.harga_atribut !== undefined ? row.harga_atribut.toLocaleString("kr-KO"): 0}</td>
                <td style={{ textAlign: "right" }}>{row.harga_gram !== undefined ? row.harga_gram.toLocaleString("kr-KO"): 0}</td>
                <td style={{ textAlign: "right" }}>{row.harga_total !== undefined ? row.harga_total.toLocaleString("kr-KO"): 0}</td>
              </tr>
            ))}
          </tbody>
          <tfoot>
          <tr>
            <td colSpan="4" style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "center" }}>
              Total Penjualan :  {item.detail.length} 
            </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.berat).reduce((a,b) => a+b,0)}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.ongkos).reduce((a,b) => a+b,0).toLocaleString("kr-KO")}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.harga_jual).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.berat_atribut).reduce((a,b) => a+b,0).toLocaleString("kr-KO")}  </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.harga_atribut).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.harga_gram).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </td>
            <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}> {item.detail.map(list => list.harga_total).reduce((a,b) => a+b,0).toLocaleString("kr-KO")} </td>
            </tr>
            <tr>
                <td colSpan="12"></td>
            </tr>
            </tfoot>
          </>
          
        ))}
              
        </table>
      </>
    );
  }
}
export default ExportPenjualanSalesExel;
