import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { GetDataGroup, getDataSales } from "../../../../actions/datamaster_action";
import {
  InputDate,
  ReanderSelect,
} from "../../../../components/helpers/field";
import { formatGram, getTglSystem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiLaporanPenjualanSales from "../../../../Validasi/Penjualan/ValidasiLaporanPenjualanSales";
import ExportPenjualanSalesExel from "./exel/ExportPenjualanSalesExel";
import ExportLaporanPenjualanSales from "./pdf/ExportLaporanPenjualanSales"
import Loading from 'react-fullscreen-loading';
import ExportPenjualanSalesExelRekap from "./exel/ExportPenjualanSalesExelRekap";
import ExportLaporanPenjualanSalesRekap from "./pdf/ExportLaporanPenjualanSalesRekap";

// const maptostate = (state) => {
//   return {
//     datasales: state.datamaster.getDataSales,
//   };
// };

class HeadFormLaporanPenjualanSales extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      isRekap: false
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }
  exportPdf() {
    (!this.props.isRekap) ? ExportLaporanPenjualanSalesRekap(this.props.LaporanPenjualanSales)  : ExportLaporanPenjualanSales(this.props.LaporanPenjualanSales);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  componentDidMount() {
    this.props.dispatch(getDataSales());
    // let hasil = { name: 'Swedish', value: 'sv' }
    this.props.change('kode_sales',"SEMUA")
    this.props.change('type',"REKAP")
    this.props.change("kode_group", "ALL");
    this.props.change("jenis_group", "ALL");
    this.props.dispatch(GetDataGroup());
  }
  render() {
    let datagroup = [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ]
    this.props.GetDataGroup.forEach(list => {
      let row = {
        name : list.kode_group,
        value : list.kode_group,
      }
      datagroup.push(row) 
    });
    let databarang = [];
    let berat = 0;
    let ongkos = 0;
    let harga_jual = 0;
    let harga_total = 0;
    let harga_gram = 0;
    this.props.LaporanPenjualanSales.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat = berat + detail.berat;
        ongkos = ongkos + detail.ongkos;
        harga_jual = harga_jual + detail.harga_jual;
        harga_total = harga_total + detail.harga_total;
        harga_gram = harga_gram + detail.harga_gram;
      });
    });
    const columns = [
      {
        dataField: "no_faktur_jual",
        text: "NO FJ",
        footerAttrs: {
          colSpan: "3",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang : {databarang.length} </div>;
        },
      },
      {
        dataField: "kode_barcode",
        text: "Kode Barcode",
       
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return <div className="text-right">{formatGram(berat,3)} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell,3)} </div>;
        },
      },
      {
        dataField: "ongkos",
        text: "Ongkos",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{ongkos.toLocaleString("kr-KO")} </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell === undefined ? 0 : cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_jual",
        text: "Harga Jual",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_jual.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell === undefined ? 0 : cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_gram",
        text: "Harga/Gram",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_gram.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell === undefined ? 0 : cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_total.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell === undefined ? 0 : cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
    ];
    const columnsRekap = [
      {
        dataField: "kode_sales",
        text: "Kode Sales",
        footer: "",
       
      },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return <div className="text-right">{formatGram(berat,3)} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell,3)} </div>;
        },
      },
      {
        dataField: "ongkos",
        text: "Ongkos",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{ongkos.toLocaleString("kr-KO")} </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_total.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
    ];
    let sales = [
      {
        value: "SEMUA",
        name : "SEMUA"
      }
    ]
    this.props.datasales.map((row) => {
      let data = {
        value: row.kode_sales,
        name: row.kode_sales + " - " + row.nama_sales,
      };
      sales.push(data);
      return true
    })
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-lg-3">
            <Field
              id="tgl_awal"
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-3 setLastDate">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Kode Sales"
              name="kode_sales"
              options={sales}
              placeholder={"Silahkan Pilih Nama Sales"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="type"
              component={ReanderSelect}
              // onChange={(e)=>this.props.onChange(e)}  
              options={[
                { value: "DETAIL", name: "Detail" },
                { value: "REKAP", name: "Rekap" },
              ]}
              label="Type"
              placeholder="Pilih Type"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="kode_group"
              component={ReanderSelect}
              options={datagroup}
              onChange={(e)=>this.setState({kode_group : e})}
              label="Kode Group"
              placeholder="Pilih Kode Group"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="jenis_group"
              component={ReanderSelect}
              options={
                [
                  {
                    value : "ALL",
                    name : "SEMUA",
                  },
                  {
                    value : "MUDA",
                    name : "MUDA",
                  },
                  {
                    value : "TUA",
                    name : "TUA",
                  }
                ]
              }
              label="Jenis Group"
              placeholder="Pilih Jenis Group"
            />
          </div>
          <div className="col-lg-4 mb-4">
            <label>&nbsp;</label>
            <div className="text-right">
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? databarang : []}
              columns={this.props.isRekap ? columns : columnsRekap}
              empty={true}
              textEmpty="Data Laporan Transaksi Penjualan Sales Kosong"
            />
          </div>
        <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
          {/* <label htmlFor="">&nbsp;</label> */}
          <div className="row">
            <div className="col-lg-6">
                <button
                  type="button"
                onClick={() => this.exportPdf()}
                className="btn btn-warning btn-block"
              >
                {" "}
                Export PDF{" "}
              </button>
            </div>
            <div className="col-lg-6">
              {(this.props.isRekap) ? <ExportPenjualanSalesExel data={this.props.LaporanPenjualanSales} /> 
              : <ExportPenjualanSalesExelRekap data={this.props.LaporanPenjualanSales} />}
            </div>
          </div>
        </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadFormLaporanPenjualanSales = reduxForm({
  form: "HeadFormLaporanPenjualanSales",
  enableReinitialize: true,
  validate: ValidasiLaporanPenjualanSales,
})(HeadFormLaporanPenjualanSales);
// export default connect(maptostate, null)(HeadFormLaporanPenjualanSales);
export default connect((state) => {
  return {
    datasales: state.datamaster.getDataSales,
    GetDataGroup: state.datamaster.GetDataGroup,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanPenjualanSales);